body {
  margin: 0px;
  font-family: 'Titillium Web', sans-serif;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
}

textarea {
  border: 1px solid #26bfd4;
  background-color: transparent;
  font-size: 1rem;
  height: 100px;
  outline: none;
  padding: 10px;
}

.select-element {
  border: 1px solid #ccc;
  font-size: 0.8rem;
  width: 100%;
  border-radius: 3px;
  overflow: hidden;
  background-color: transparent;
  outline: none;
}

.btn {
  curser: pointer;
  height: 42px;
  font-size: 1em;
  font-weight: 500;
  border: 1px solid transparent;
  transition: all 0.5s ease-in-out;
  background-color: #26bfd4;
  color: white;
}

.btn:active, .btn:focus {
  outline: none;
}

.btn:hover {
  background-color: #207b88;
}

.one-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
}

.two-columns {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr;
}

.three-columns {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr 1fr;
}

.nav-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 30px;
}

.nav-wrapper .left-side {
  display: flex;
}

.nav-wrapper .right-side a {
  margin-left: 15px;
  cursor: pointer;
  color: #26bfd4;
  transition: 0.5 ease-in-out;
}

.nav-wrapper .right-side a:hover {
  color: #207b88;
}

.nav-wrapper .nav-link-wrapper {
  height: 22px;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
  margin-right: 20px;
  text-transform: uppercase;
  font-size: 0.9rem;
}

.nav-wrapper .nav-link-wrapper a {
  color: #8a8a8a;
  text-decoration: none;
}

.nav-wrapper .nav-link-wrapper a:hover {
  color: black;
}

.nav-wrapper .nav-link-wrapper:hover {
  border-bottom: 1px solid black;
}

.nav-wrapper .nav-link-wrapper .nav-link-active {
  border-bottom: 1px solid black;
  color: black;
}

.portfolio-items-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.portfolio-items-wrapper .portfolio-item-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.portfolio-items-wrapper .portfolio-item-wrapper .portfolio-img-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 100%;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  height: 100%;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .subtitle {
  transition: 1s ease-in-out;
  color: transparent;
}

.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper:hover .subtitle {
  color: #26bfd4;
  font-weight: 400;
}

.portfolio-items-wrapper .portfolio-item-wrapper .logo-wrapper img {
  width: 50%;
  margin-bottom: 20px;
}

.image-blur {
  transition: 1s;
  filter: brightness(10%);
}

.portfolio-manager-wrapper {
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.portfolio-manager-wrapper .left-column {
  background-color: #f6f6f6;
}

.portfolio-manager-wrapper .right-column {
  background-color: #42454a;
}

.portfolio-form-wrapper {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  padding: 42px;
}

.portfolio-form-wrapper input {
  font-family: "Titillium Web", sans-serif;
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #26bfd4;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}

.portfolio-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #207b88;
}

.portfolio-form-wrapper input {
  margin-bottom: 0px;
}

.portfolio-form-wrapper .image-uploaders {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 200px 200px 200px;
}

.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  border-radius: 5px;
}

.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper .image-removal-link {
  display: flex;
  justify-content: center;
}

.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper .image-removal-link a {
  font-weight: 900;
  color: #922a2a;
  cursor: pointer;
  transition: 0.5 ease-in-out;
}

.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper .image-removal-link a:hover {
  color: red;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb {
  padding: 21px;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .portfolio-thumb-img img {
  width: 100%;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .title {
  color: #f6f6f6;
  font-size: 1.5em;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .action-icon {
  cursor: pointer;
  color: #f6f6f6;
  font-size: 1.5em;
  transition: 0.5s ease-in-out;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .action-icon:hover {
  color: #922a2a;
}

.portfolio-detail-wrapper {
  width: 100vw;
}

.portfolio-detail-wrapper .banner {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.3);
}

.portfolio-detail-wrapper .portfolio-detail-description-wrapper {
  text-align: center;
  margin-top: 42px;
  display: flex;
  justify-content: center;
}

.portfolio-detail-wrapper .portfolio-detail-description-wrapper .description {
  width: 700px;
  font-size: 2em;
  margin-bottom: 50px;
}

.portfolio-detail-wrapper .bottom-content-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio-detail-wrapper .bottom-content-wrapper .site-link {
  padding: 15px;
  background-color: #26bfd4;
  border-radius: 3px;
  text-decoration: none;
  color: white;
  transition: 0.5s ease-in-out;
  font-size: 1.2em;
}

.portfolio-detail-wrapper .bottom-content-wrapper .site-link:hover {
  background-color: #207b88;
}

.homepage-wrapper {
  display: grid;
  grid-template-columns: 1fr;
}

.homepage-wrapper .space {
  height: 20px;
}

.homepage-wrapper .filter-links {
  display: flex;
  justify-content: space-around;
}

.homepage-wrapper .filter-links .btn {
  width: 100%;
}

.auth-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 84px);
}

.auth-page-wrapper .left-column {
  background-size: cover;
}

.auth-page-wrapper .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
}

.auth-page-wrapper .right-column .auth-form-wrapper {
  display: grid;
  grid-gap: 21px;
  background-color: #f6f6f6 !important;
  grid-gap: 21px;
  padding: 42px;
}

.auth-page-wrapper .right-column .auth-form-wrapper input {
  font-family: "Titillium Web", sans-serif;
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #26bfd4;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}

.auth-page-wrapper .right-column .auth-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #207b88;
}

.auth-page-wrapper .right-column .auth-form-wrapper input:focus {
  background-color: #f6f6f6 !important;
}

.auth-page-wrapper .right-column .auth-form-wrapper .form-group {
  display: flex;
}

.auth-page-wrapper .right-column .auth-form-wrapper .form-group svg {
  font-size: 2em;
  color: #26bfd4;
  margin-right: 15px;
}

.blog-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.blog-container .admin-blog-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.blog-container .admin-blog-wrapper a {
  margin-left: 15px;
  cursor: pointer;
}

.blog-container .new-blog-link {
  position: fixed;
  right: 0;
  top: 10;
}

.blog-container .new-blog-link a {
  font-size: 3em;
  cursor: pointer;
  color: #26bfd4;
  margin-right: 15px;
  transition: 0.5s ease-in-out;
}

.blog-container .new-blog-link a:hover {
  color: #207b88;
}

.blog-container .content-container {
  display: flex;
  flex-direction: column;
  width: 800px;
}

.blog-container .content-container a {
  text-decoration: none;
  color: #26bfd4;
  transition: 0.5s ease-in-out;
}

.blog-container .content-container a:hover {
  color: #207b88;
}

.blog-container .content-container h1 {
  font-size: 1.5em;
}

.blog-container .content-container .featured-image-wrapper img {
  margin: 15px 0px;
  width: 100%;
  height: 350px;
  border-radius: 5px;
  object-fit: cover;
}

.content-loader {
  font-size: 2em;
  color: #26bfd4;
}

.blog-form-wrapper {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  padding: 42px;
}

.blog-form-wrapper input {
  font-family: "Titillium Web", sans-serif;
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #26bfd4;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}

.blog-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #207b88;
}

.blog-form-wrapper input {
  margin-bottom: 0px;
}

.blog-form-wrapper .image-uploaders {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 200px;
}

.DraftEditor-root {
  border: 1px solid #f1f1f1;
  padding: 20px;
  border-radius: 3px;
}

.rdw-colorpicker-modal {
  left: -83px;
}

.about-page-wrapper {
  display: flex;
  height: calc(100vh - 84px);
}

.about-page-wrapper .about-image {
  display: flex;
  justify-content: center;
  background-size: cover;
  background-position: center;
  width: 50%;
}

.about-page-wrapper .about-bio {
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5%;
  width: 50%;
}

@media (max-width: 1050px) {
  .about-page-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 450px auto;
  }
  .about-page-wrapper .about-image {
    width: 70%;
    justify-self: center;
    margin: 0px;
  }
  .about-page-wrapper .about-bio {
    width: 90%;
  }
}

@media (min-width: 700px) and (max-width: 1050px) {
  .about-page-wrapper {
    grid-template-rows: 700px auto;
  }
}

.contact-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 84px);
}

.contact-page-wrapper .contact-image {
  background-size: cover;
  background-position: center;
}

.contact-page-wrapper .right-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  padding: 59px;
}

.contact-page-wrapper .bullet-point-group {
  display: flex;
  align-items: center;
}

.contact-page-wrapper .bullet-point-group .icon {
  font-size: 2em;
  color: #26bfd4;
}

.contact-page-wrapper .bullet-point-group .text {
  font-size: 1.5em;
  font-weight: 900;
  margin-left: 21px;
}

@media (max-width: 1050px) {
  .contact-page-wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 200px 100px;
  }
  .contact-page-wrapper .contact-image {
    display: none;
  }
  .contact-page-wrapper .right-column {
    width: 90%;
    grid-row: 1;
    padding: 5%;
  }
}

form {
  margin-top: 80px;
  width: 500px;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-group input {
  font-family: "Titillium Web", sans-serif;
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #26bfd4;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}

.input-group input:focus {
  outline: none;
  border-bottom: 1px solid #207b88;
}

.input-group:focus {
  background-color: #f6f6f6;
}

.input-group input {
  border: 1px solid #26bfd4;
  padding: 10px;
  width: 478px;
}

.input-group #message {
  font-size: 1.1em;
  font-family: "Titillium Web", sans-serif;
}

.btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

#contact-form-btn {
  width: 200px;
  margin-top: 20px;
}

@media (max-width: 699px) {
  .contact-page-wrapper form {
    width: 300px;
  }
  .contact-page-wrapper .input-group input {
    width: 278px;
  }
}
